import Icon28User from '@vkontakte/icons/dist/28/user';
import Icon28Settings from '@vkontakte/icons/dist/28/settings';
import Icon28Message from '@vkontakte/icons/dist/28/message';
import Icon28Users from '@vkontakte/icons/dist/28/users';
import React from 'react';
import connect from '@vkontakte/vkui-connect';
import axios from 'axios';
import './css/props.css';
// eslint-disable-next-line
import {
  View, Div, PanelHeader, Epic, ModalRoot, ModalPage, ModalPageHeader, List, Cell, Avatar, HeaderButton, Group,
  Tabbar, ScreenSpinner, Panel, TabbarItem
} from '@vkontakte/vkui';
// eslint-disable-next-line
import { IS_PLATFORM_ANDROID, IS_PLATFORM_IOS } from '@vkontakte/vkui/dist/lib/platform';
import '@vkontakte/vkui/dist/vkui.css'
import Cards from './js/panels/main/Base';
import Profile from './js/panels/profile/Profile';


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      debug: true,
      activeStory: { view: 'loader', panel: 'loader' },
      activeModal: null,
      pairs: [],
      history: [{
        view: 'loader',
        panel: 'loader'
      }],
      user: undefined,
      userInterests: undefined,
      debugServerInfo: 'default',
      userSettings: undefined
    };
    this.onStoryChange = this.onStoryChange.bind(this);
    this.inited = this.inited.bind(this);
    this.saveUserSettings = this.saveUserSettings.bind(this);
    this.syncInterests = this.syncInterests.bind(this);

  }
  componentDidMount() {
    connect.subscribe((e) => {
      switch (e.detail.type) {
        case 'VKWebAppGetUserInfoResult':

          connect.send('VKWebAppAllowNotifications', {})
          connect.send("VKWebAppGetGeodata", {});
          this.setState({ user: e.detail.data });
          var t = this;
          axios.post(`https://salert.info/user/GetUserData`, e.detail.data).then(
            (response) => {
              var debug = t.state.debugServerInfo;
              debug += `GetUserData  ${JSON.stringify(response)}`;
              t.setState({ debugServerInfo: debug });
              t.inited();
              axios.get(`https://salert.info/user/GetUserSettings/${e.detail.data.id}`).then((x) => {
                t.setState({ userSettings: x.data });
                t.inited();
              }).catch((x) =>
                t.setState({ userSettings: x })
              )
              axios.get(`https://salert.info/interests/GetInterests/${e.detail.data.id}`
              ).then((x) => {
                t.setState({ userInterests: x.data });
                t.inited();
              }).catch(function (x) {
              });
            }
          ).catch((response) => {
            var debug = t.detail.data;
            debug += JSON.stringify(response);
            t.setState({ debugServerInfo: debug });
          });
          break;
        case 'VKWebAppGetUserInfoFailed':
          var debug = JSON.stringify(e.detail);
          t.setState({ debugServerInfo: debug });
          break;
        case 'VKWebAppGeodataResult':
          var response = e.detail.data;
          if (response.available == 1) {
            var coords = {
              lat: response.lat,
              long: response.long,
              userId: this.state.user.id
            };
            axios.post(`https://salert.info/user/setGeoData`, coords
            ).then((x) => {
            }).catch(function (x) {
            });
          }
          break;
        case 'VKWebAppAllowNotificationsResult':
          var model = {
            userId: this.state.user.id,
            isAvilable: e.detail.data.result
          };
          axios.post(`https://salert.info/user/SetNotifications`, model
          ).then((x) => {
          }).catch(function (x) {
          });

        default:
          console.log(e.detail.type);
      }
    });
    connect.send('VKWebAppGetUserInfo', {});
  }
  saveUserSettings(userSettings) {
    var t = this;
    axios.post(`https://salert.info/user/SaveUserSettings`, userSettings).then(
      (response) => {
        t.setState({ debugServerInfo: JSON.stringify(response), userSettings: response.data, activeStory: { view: 'info', panel: 'info' } });

      }
    ).catch((response) => {
      t.setState({ debugServerInfo: JSON.stringify(response) });

    });

  }
  syncInterests(interests) {
    this.setState({ userInterests: interests, debugServerInfo: JSON.stringify(interests) });
  }
  inited() {
    if (this.state.userInterests != undefined && this.state.userSettings != undefined)
      this.setState({ activeStory: { view: 'info', panel: 'info' } });
  }
  onStoryChange(e) {
    var c = this.state.activeStory;
    c.view = e.currentTarget.dataset.view;
    this.setState({ activeStory: c })
  }
  addPair(user) {
    var newPairs = this.state.pairs;
    newPairs.push(user);

    this.setState({ pairs: newPairs });
  }

  render() {
    const { user, pairs, debugServerInfo, userSettings, userInterests } = this.state;

    return (
      <Epic activeStory={this.state.activeStory.view} tabbar={
        <Tabbar>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory.view === 'info'}
            data-view='info'
            text="Профиль"
          ><Icon28User /></TabbarItem>

          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory.view === 'cards'}
            data-view='cards'
            text="Карточки"
          ><Icon28Users /></TabbarItem>


          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory.view === 'base'}
            data-view='base'
            label={pairs.length.toString()}
            text="Сообщения"
          ><Icon28Message /></TabbarItem>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory.view === 'settings'}
            data-view='settings'
            text="Настройки"
          ><Icon28Settings /></TabbarItem>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory.view === 'debug'}
            data-view='debug'
            text="Дебаг"
          ><Icon28Settings /></TabbarItem>
        </Tabbar>
      }>
        <View id='loader' popout={<ScreenSpinner />} activePanel="loader">
          <Panel id='loader'>
            <PanelHeader>Загрузка...</PanelHeader>
          </Panel>
        </View>
        <Profile syncInterests={this.syncInterests} userInterests={userInterests} saveUserSettings={this.saveUserSettings} userSettings={userSettings} id='info' user={user}></Profile>
        <Cards id='cards' addPair={this.addPair} user={user} userInterests={userInterests}></Cards>

        <View id='debug' activePanel='debug'>
          <Panel id='debug' >
            <PanelHeader>Debug</PanelHeader>
            <Div>
              {this.state.debugServerInfo}
            </Div>

          </Panel>
        </View>



      </Epic>
    )
  }
}

export default App;